  },

  methods: {
    openClose(ref) {

      if(this.oldOpened === ref) {
        this.$refs[ref].querySelector('.consultas--sanfona__obj-content').style.height = '0';
        this.$refs[ref].querySelector('.consultas--sanfona__obj-head--icon').style.transform = 'rotate(0deg)';
