      }
      else {
        this.$refs[ref].querySelector('.consultas--sanfona__obj-content').style.height =
          this.$refs[ref].querySelector('.consultas--sanfona__obj-content .consultas--sanfona__obj-content--txt').offsetHeight+'px';
      
        this.$refs[ref].querySelector('.consultas--sanfona__obj-head--icon').style.transform = 'rotate(90deg)'
        console.log(this.$refs[ref].querySelector('.consultas--sanfona__obj-head--icon').style.transform)
        
        if (this.oldOpened) {
          this.$refs[this.oldOpened].querySelector('.consultas--sanfona__obj-content').style.height = '0';
          this.$refs[this.oldOpened].querySelector('.consultas--sanfona__obj-head--icon').style.transform = 'rotate(0deg)'
        }
        this.oldOpened = ref;
      }
    }
  }

}
