<template>
  <section class="blog" id="blog">
    <div class="blog--imgs">
      <div v-for="(info, index) in blogInfos "
        class="blog--imgs-img" :style="{'background-image': 'url('+info.img+')'}"
        @click="gotoBlog(index)"
        :key="index">
        <div class="blog--imgs-img_box">
          <p class="blog--imgs-img_txt" v-html="info.title"></p>
          <!-- <p class="blog--imgs-img_bold" v-html="info.bold"></p> -->
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  data() {
    return {
      blogInfos: []
    }
  },
  methods: {
    gotoBlog(idx) {
      this.$router.push(`/blog/${idx}`);
    }
  },
  beforeMount() {
    for(let i = 0; i < 4; i++) {
      if(this.$store.state.data.blog[i]) {
        this.blogInfos.push(this.$store.state.data.blog[i]);
      }
    }    
  }
}
</script>

