<script>
import mixin from '../mixin'

export default {
  mixins: [mixin],
  name: 'MobileMenu',
  props: {
    show: Boolean
  },
  methods: {
    clickOps(op) {
      this.$emit('toggleMenu', !this.show);
      setTimeout(() => {
        this.$emit('gotoPlace', op, this.$route.name === 'blog');
      }, 600);
    },
    gohome() {
      this.$emit('toggleMenu', !this.show);
      this.$router.push('/');
    },

  }
}
