<template>
  <div id="app">
    <div v-if="loaded">
      <HeaderClaudia
        :opened="opened" @toggleMenu="toggleMenu"
        @gotoPlace="gotoPlace"/>
      <MobileMenu
        :show="opened" @toggleMenu="toggleMenu"
        @gotoPlace="gotoPlace"/>
      <!-- <nav>
        <router-link to="/">Home</router-link> |
        <router-link to="/about">About</router-link>
      </nav> -->
      <router-view/>
      <UpBtn/>
    </div>
    <div v-else>
      <PreloaderClaudia/>
    </div>
  </div>
</template>
<script>
import HeaderClaudia from './components/HeaderClaudia.vue'
import MobileMenu from './components/MobileMenu.vue'
import PreloaderClaudia from './components/PreloaderClaudia.vue'
import UpBtn from './components/UpBtn.vue'

export default {
  // mixins: [mixin],
  components: {
    HeaderClaudia,
    MobileMenu,
    PreloaderClaudia,
    UpBtn
  },
  data() {
    return {
      opened: false,
      loaded: false
    }
  },
  beforeMount() {
    // 'https://api.npoint.io/fe3ac7facbab4a8418d4'
    // 'https://api.npoint.io/5d3c7d54f3f87d91ad2a'
    // https://api.npoint.io/48c01b6994755f2c30e9
    this.$http.get('https://squaq.github.io/blog.json')
      .then((result) => {
        setTimeout(() => {
          this.loaded = true;
          this.$store.commit('setdata', result.data);
          if(this.$route.params.place) {
            setTimeout(() => this.gotoPlace(this.$route.params.place, true), 0) 
          }
        }, 2000);
      })
  },
  methods: {
    toggleMenu(opened) {
      this.opened = opened;
      const bo = document.querySelector('body')
      if(opened) {
        bo.classList.add('stopScroll');
        window.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
      }
      else bo.classList.remove('stopScroll')
    },

    gotoPlace(op, fromBlog) {
      
      if(fromBlog) {
        this.$router.replace(`/`);
      }


      setTimeout(() => {
        const el = document.querySelector('#'+op)
        if (el) {
          this.$router.replace(`/${op}`);
          el.scrollIntoView({ behavior: "smooth" });
        }
      }, 0);
    }
  }
}
</script>

