import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import Vuex from 'vuex'

Vue.use(Vuex)
Vue.prototype.$http = axios
Vue.config.productionTip = false

const store = new Vuex.Store({
  state: {
    data: null
  },
  mutations: {
    setdata (state, data) {
     state.data = data;
    }
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
