<template>
  <div class="btn" v-if="showBtnUp" @click="goUp()"></div>
</template>
<script>
export default {
  data() {
    return {
      showBtnUp: false
    }
  },

  mounted() {
    this.onScroll();
  },

  methods: {
    onScroll() {
      window.onscroll = () => {
        if(window.pageYOffset > 100) { this.showBtnUp = true; }
        else if(window.pageYOffset < 100) { this.showBtnUp = false; }
      }
    },
    goUp() {
      this.$router.replace('/');
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    }
  }
}
</script>

